body {
  margin: 0;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(180deg,#050b18,#020617);
  color: #e5e7eb;
}

nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 18px;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
}

nav a {
  color: #38bdf8;
  text-decoration: none;
  font-weight: 600;
}

nav a:hover {
  color: #a5f3fc;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 80px 20px;
}

.hero {
  text-align: center;
  padding: 120px 20px;
}

.hero h1 {
  font-size: 48px;
  background: linear-gradient(90deg,#38bdf8,#22d3ee);
  -webkit-background-clip: text;
  color: transparent;
}

.hero p {
  font-size: 18px;
  max-width: 700px;
  margin: auto;
  opacity: 0.9;
}

.section-title {
  text-align: center;
  margin-bottom: 40px;
  font-size: 32px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  gap: 25px;
}

.card {
  background: rgba(255,255,255,0.06);
  padding: 30px;
  border-radius: 20px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(56,189,248,0.2);
}

.whatsapp {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #22c55e;
  color: #fff;
  padding: 14px 20px;
  border-radius: 999px;
  text-decoration: none;
  font-weight: bold;
}
